import { Movie } from './../../_models/movie.model';
import { Router } from '@angular/router';
import { ServerService } from './../../_services/server.service';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-movies',
  templateUrl: './movies.component.html',
  styleUrls: ['./movies.component.css']
})
export class MoviesComponent implements OnInit {
  movies: Movie[];
  movie: Movie;

  @Output() movieEmitter = new EventEmitter<Movie>();
  @Output() detailClickedEmitter = new EventEmitter<boolean>();

  constructor(private serverService: ServerService, private router: Router) { }

  ngOnInit() {
    this.serverService.getCurrentAccount().
      subscribe(
        (response) => {
          this.serverService.getAllMovies().
            subscribe(
              (response) => {
                this.movies = response.json();
              }, (error) => {

              }
            );
        },
        (error) => {
          console.log('You need to log in!' + error);
          this.router.navigate(['/login']);
        }
      );
  }

  onDetailClick(id: string) {
    this.movie = this.movies.find(movie => movie.id === id);
    this.movieEmitter.emit(this.movie);
    this.detailClickedEmitter.emit(true);
  }

  onRentClick(movieId: string, movieName: string) {
    this.serverService.addRent(movieId, movieName);
  }
}



