import { Rent } from './../_models/rent.model';
import { ServerService } from './../_services/server.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})
export class OrdersComponent implements OnInit {
  orders: Rent[];
  orderHistory: Rent[];

  constructor(private serverService: ServerService) { }

  ngOnInit() {
    this.serverService.getAllNotOrderedRents()
      .subscribe(
        (response) => {
          this.orders = response.json();
        }, (error) => {
          console.log('You need to log in!' + error);
        }
      );
    this.serverService.getAllOrderedRents()
      .subscribe(
        (response) => {
          this.orderHistory = response.json();
        }, (error) => {
          console.log('You need to log in!' + error);
        }
      );
  }

  onDelete(id) {
    this.serverService.deleteRent(id);
  }

  onOrder(){
    this.serverService.completeOrder();
    location.reload();
  }

}



