import { ServerService } from './_services/server.service';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { Routes, RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { HomeComponent } from './home/home.component';
import { SignupComponent } from './signup/signup.component';
import { UnverifiedComponent } from './unverified/unverified.component';
import { MovieComponent } from './movie/movie.component';
import { MoviesComponent } from './movie/movies/movies.component';
import { MovieDetailComponent } from './movie/movie-detail/movie-detail.component';
import { HeaderComponent } from './header/header.component';
import { StartComponent } from './start/start.component';
import { BlankComponent } from './blank/blank.component';
import { OrdersComponent } from './orders/orders.component';

const routes: Routes = [
  { path: '', component: StartComponent },
  { path: 'login', component: LoginComponent },
  { path: 'home', component: HomeComponent },
  { path: 'signup', component: SignupComponent },
  { path: 'unverified', component: UnverifiedComponent },
  { path: 'movie', component: MovieComponent },
  { path: 'movies', component: MoviesComponent },
  { path: 'movie-detail', component: MovieDetailComponent },
  { path: 'orders', component: OrdersComponent },
  { path: 'error', component: BlankComponent }
]

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    HomeComponent,
    SignupComponent,
    UnverifiedComponent,
    MovieComponent,
    MoviesComponent,
    MovieDetailComponent,
    HeaderComponent,
    StartComponent,
    BlankComponent,
    OrdersComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(routes, { useHash: true })
  ],
  providers: [ServerService],
  bootstrap: [AppComponent]
})
export class AppModule { }



