import { Response } from '@angular/http';
import { ServerService } from './../_services/server.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  username: string;
  password: string;
  error = '';

  ngOnInit() {
    this.serverService.clearLocalStorage();
  }

  constructor(private serverService: ServerService, private router: Router){}

  requestOauthTokenFormAction(){
    this.serverService.requestOauthToken(this.username, this.password).
    subscribe(
      (response) => {
        this.serverService.saveOauthTokenToStorage(response.json().access_token);
        this.serverService.saveUserNameToStorage(this.username);
        location.reload();
        this.router.navigate(['home']);
      },
      (error) => {
        this.error = 'Bad creditentials! Please enter again!';
      }
    );
  }
}



